body{
  background-color: #fff4e6;
}

.App{
  background-color: #be9b7b;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: #ffffff;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
}

#to-do-form input::placeholder{
  color: grey;
}
#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #3c2f2f ;
  font-weight: bolder;
  color: #ffffff;
}